import React, {Component} from 'react';
import {connect} from 'react-redux';
import MulticartBillingAccountContract from './MulticartBillingAccountContractComponent';
import {setBillingAccountContractsVisibility} from '../../actions/app';


class MulticartBillingAccountContractsComponent extends Component {

    constructor(props) {
        super(props);
    }
    closeForm() {
        this.props.setBillingAccountContractsVisibility(false);
    }

    render() {
        return (

            <div className="opl-tree opl-tree__light-grey">
                <div className="l-row u-padding-top-l u-padding-l">
                    <div className="l-col l-col-12">
                        <div className="l-row">
                            <div className="l-col l-col-12">
                                <a href="#" className="u-right" onClick={this.closeForm.bind(this)}>
                                                   <span title="close" aria-hidden="true"
                                                         className="kss-icon-preview g-icon g-icon--only g-icon--xs-s g-icon--close"></span></a>
                            </div>
                        </div>
                    </div>
                </div>
                <ul className="opl-tree__list">
                    <li className="opl-tree__li opl-tree__li--last">
                        <div className="l-row u-spacing-top-l">
                            <div className="l-col l-col-small-12 l-col-medium-4 l-col-4 ">
                                <p className="h3 opl-tree__branch">
                                    <span className="opl-tree__square-list"></span>
                                    Usługi komórkowe {this.props.account.type==="MAGNUM"?"(z aktywnym Orange Love)":""}</p>
                            </div>
                            <div className="l-col l-col-small-12 l-col-medium-4 l-col-4 ">
                                <h6>{this.props.account.addressStreetLine}</h6>
                                <h6>{this.props.account.addressCityLine}</h6>
                            </div>
                        </div>
                        <ul className="opl-tree__list">
                            {this.props.accountContracts.map(contract =>
                                <MulticartBillingAccountContract key={contract.msisdn} msisdn={contract.msisdn} dateTo={contract.dateTo}/>
                            )}
                        </ul>
                    </li>
                </ul>
            </div>

        )
    }
}

export default connect(null, {setBillingAccountContractsVisibility})(MulticartBillingAccountContractsComponent);
